<template>
  <div class="">
    <el-card class="table">
      <el-form :model="queryParams" :rules="rules" ref="formRef">
        <el-form-item prop="title" label="文章标题">
          <el-input
            v-model="queryParams.title"
            clearable
            placeholder="请输入文章标题"
          ></el-input>
        </el-form-item>
        <el-form-item prop="description" label="文章描述">
          <el-input
            v-model="queryParams.description"
            clearable
            placeholder="请输入文章描述"
          ></el-input>
        </el-form-item>
      </el-form>
      <el-tabs v-model="activeName">
        <el-tab-pane name="first" label="markdown">
          <!-- 自定义事件successFn -->
          <Markdown @success="successFn" ref="markdownRef"></Markdown>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script setup>
import { ref } from "vue";
import Markdown from "./components/Markdown.vue";// 子组件
import { addArticle } from "@/api/article";// 接口
import { ElMessage } from "element-plus";// 提示信息
// 准备要上传的数据 router.post('/addArticle', async (req, res) => {
//   const {
  // 标题
//     title,
//     description,
//     content
//   } = req.body
const queryParams = ref({
  title: "",
  description: "",
  content: "",
});
const activeName = ref("first");
const formRef = ref(null);
const rules = ref({
  title: [{ required: true, trigger: "blur", message: "文章标题是必填项" }],
  description: [{ required: true, trigger: "blur", message: "文章描述是必填项" }],
});
const markdownRef = ref(null);
// 给组件添加的自定义事件 子组件提交触发 成功就会吧你写的内容传过来
const successFn = (content) => {
  // 传入参数content （你写的内容）
  // console.log(content); 接受的是 你输入后点击提交后的内容
  // 例如你输入 22333  打印的是 组件Markdown中提交按钮触发的函数运行的结果:<p>2332323</p>
  // 然后将其传给queryParams.value.content
  queryParams.value.content = content;
  // 然后发起请求
  formRef.value.validate(async (value) => {
    if (value) {
      // 就收传过来的数据
      const { data } = await addArticle(queryParams.value);
      if (!data.code) {
        ElMessage.success(data.txt);  // 成功了就弹窗说成功
        formRef.value.resetFields();  // 表单接着清空里面的内容 
        markdownRef.value.clearEditor();  // 清空markdown内容子组件定义一个清空的函数 在导出
      } else {
        // 失败提示
        ElMessage.error(data.txt);
      }
    }
  });
};
</script>

<style lang="scss" scoped>
:deep(.el-input) {
  width: 50% !important;
}
</style>
