<template>
  <div class="markdown-container">
    <div id="markdown-box" ref="boxRef"></div>
    <div class="bottom">
      <el-button type="primary" @click="submitFn">提交</el-button>
    </div>
  </div>
</template>
<script setup>
// npm i @toast-ui/editor
import { nextTick, ref, onMounted, defineExpose, defineEmits } from "vue";

import MKEditor from "@toast-ui/editor";// 使用插件
import "@toast-ui/editor/dist/toastui-editor.css";//引入样式
import "@toast-ui/editor/dist/i18n/zh-cn";//引入汉化功能
import { ElMessage } from "element-plus";

const emit = defineEmits(["success"]); // 获取父给子的自定义事件
const boxRef = ref(null);//方便操作dom
let mkEditor;//初始化实例
// 1.初始化插件
const initEditor = () => {
  mkEditor = new MKEditor({
    el: boxRef.value,// 自带的配置项
    height: "400px",    // 需要的高度
    previewStyle: "vertical",  // 垂直的
    language: "zh-CN",  // 语言
  });
 
  mkEditor.getMarkdown(); // 运行
};
// 清空内容的函数
const clearEditor = () => {
  mkEditor.reset();  // mkEditor自带的重置方法 调用就行
};

// 提交按钮
const submitFn = () => {
  // .getHTML() mkEditor自带的方法 需要内容就在里面
  const htmlStr = mkEditor.getHTML();
  // console.log(htmlStr);<p><br></p>初始值
  if (htmlStr === "<p><br></p>") {
    // 没有输入内容
    ElMessage.warning("内容不能为空");
  } else {
    // 成功就触发 父组件中的自定义事件 并将内容传过去
    emit("success", htmlStr);
  }
};
//2. 初始化插件 在挂在成功后
onMounted(async () => {
  await nextTick(); // 等待准备完成
  initEditor(); // 调用上面的函数 初始化插件
});
defineExpose({ clearEditor });
</script>
<style lang="scss" scoped>
.markdown-container {
  .bottom {
    margin-top: 20px;
    text-align: right;
  }
}
</style>
